<template>
	<view class="importWallet">
		<tabTop left-icon="back"  right-text="" title="创建钱包" :open='false' />
		<view class="form">
			<view class="upload">
				<view class="upload-img"></view>
			</view>
			<view class="taps-box">
				<view class="taps">
					<view class="taps-title border" :class="{'tapsActive':type === 1}" @click="checkout(1)">助记词</view>
					<view class="taps-title" :class="{'tapsActive':type === 2}" @click="checkout(2)">私钥</view>
				</view>
			</view>
			<view class="form-input-box">
				<!-- <formInput v-show='taps' name='请按照顺序输入助记词，使用助记词导入钱包的同时 可以重置密码' pVal='请输入助记词，并使用空格分隔' tipVal='助记词为必填项' @val='onMnemonic' />
				<formInput v-show='!taps' name='复制粘贴波场官方钱包PrivateKey件内容至输入框或 通过扫描Private Key内容生成的二维码导入，请留意 大小写' pVal='请输入私钥' tipVal='助记词为必填项' @val='onKey' />
				<formInput name='钱包名称' pVal='请输入钱包名称' tipVal='钱包名称为必填项' @val='onName' />
				<formInput name='设置密码' pVal='请输入新密码' tipVal='密码为必填项' type='password' @val='onPassWord'/>
				<formInput name='再次输入密码' pVal='再次输入密码' tipVal='确认密码为必填项' type='password' @val='onConfirmPassword'/> -->
				<formInput 
				v-show='type === 1' name='请按照顺序输入助记词，使用助记词导入钱包的同时 可以重置密码' 
				:tipVal='mnemonicError'  :hide='mnemonicHide'
				pVal='请输入助记词，并使用空格分隔' tipVal='助记词为必填项'
				@val='onMnemonic' />
				<formInput
				v-show='type === 2' name='复制粘贴波场官方钱包PrivateKey件内容至输入框或 通过扫描Private Key内容生成的二维码导入，请留意 大小写'
				:tipVal='privatekeyError'  :hide='privatekeyHide'
				pVal='请输入私钥' tipVal='私钥为必填项'
				@val='onKey' />
				<formInput name='钱包名称' pVal='请输入钱包名称' :tipVal='nicknameError' @val='onName' :hide='nicknameHide'/>
				<formInput name='设置密码' pVal='请输入新密码' :tipVal='pwdError' type='password' @val='onPassWord' :hide='pwdHide'/>
				<formInput name='再次输入密码' pVal='再次输入密码' :tipVal='confirm_pwdError' type='password' @val='onConfirmPassword' :hide='confirm_pwdHide'/>
			</view>
			<view class="next">
				<nextButton @next='next'/>
			</view>
		</view>
	</view>
</template>

<script>
	import tabTop from '@/components/tabTop.vue';
	import formInput from '@/components/formInput.vue';
	import nextButton from '@/components/nextButton.vue';
	import fromTest from '@/util/fromTest.js';
	export default{
		components: {tabTop,formInput,nextButton},
		mixins: [fromTest],
		data(){
			return{
				type:1,
				privatekey:''
			}
		},
		methods:{
			checkout(type){
				this.type = type;
			},
			showToast(){
				uni.showToast({
					title:'请完善资料！',
					icon:'none'
				})
			},
			getImportWallet(data){
				this.post('api/importWallet',data)
				.then(res =>{
					console.log(res)
				})
			},
			next(){
				const token = uni.getStorageSync('token');
				// console.log('token:',token);
				this.formData.type = this.type;
				if(this.type === 1){
					if(this.nicknamePass && this.pwdPass && this.confirm_pwdPass && this.mnemonicPass){
						const data = JSON.parse(JSON.stringify(this.formData));
						data.word = data.mnemonic;
						// console.log('1',data);
						delete data.mnemonic;
						delete data.key;
						// console.log('1',data);
						this.getImportWallet(data);
					}else{
						this.showToast();
					};
				}else{
					if(this.nicknamePass && this.pwdPass && this.confirm_pwdPass && this.privatekeyPass){
						const data = JSON.parse(JSON.stringify(this.formData));
						data.word = data.mnemonic;
						// console.log('2',data);
						delete data.mnemonic;
						// console.log('2',data);
						this.getImportWallet(data);
					}else{
						this.showToast()
					}
				}
				
				
			}
		},
		mounted(){
			// console.log(this.nicknameHide)
		}
	}
</script>

<style lang="scss">
	.importWallet{
		@include levelCen();
		@include bg();
		.form{
			@include size(650rpx,auto,none);
			background: rgba(29,43,73,.8);
			margin-top: 52rpx;
			border-radius: 10rpx;
			box-sizing: border-box;
			padding-bottom: 68rpx;
			.taps-box{
				width: 100%;
				display: flex;
				justify-content: center;
				margin-bottom: 15rpx;
				.taps{
					@include size(561rpx,56rpx,none);
					border: 1rpx #fff dashed;
					box-sizing: border-box;
					.taps-title{
						@include size(50%,100%,none);
						@include cen();
						@include font(24rpx,#fff);
						float: left;
						box-sizing: border-box;
					}
					.border{
						border-right: 1rpx dashed #fff;
					}
				}
			}
			
			
			.upload{
				height: 100rpx;
				@include cen();
				margin-top: 50rpx;
				margin-bottom: 40rpx;
				.upload-img{
					@include size(100rpx,100rpx,#2DE9B7);
					border-radius: 50%;
				}
			}
			
			.form-input-box{
				@include cen();
				flex-direction: column;
			}
			
			.next{
				margin-top: 50rpx;
			}
		}
		
		.tapsActive{
			background: linear-gradient(to right,#2A65BC,#079D74);
		}
	}
</style>
